.bookingcart-biggestdiv{
  color: white;
}
a {
  text-decoration: none;
}
.bc-row {
  display: flex;
  justify-content: center;
}
.bc-row.center {
  align-items: center;
}
.col-1 {
  flex: 1;
}
.col-2 {
  flex: 2;
}
.bc-block {
  background-color: #101113;
  padding: 1rem;
  margin: 0.5rem;
  border-radius: 3px;
  width: 100%;
}
.text-right {
  text-align: right;
}
img.small {
  max-height: 8rem;
}
.bc-button {
  font-size: 0.8rem;
  padding: 0.2rem;
  margin: 0.1rem;
  border-radius: 3px;
  border: 0.1rem #404040 solid;
  background-color: #ffc001;
  width: 100%;
  cursor: pointer;
}
.button.add {
  background-color: #40c0f0;
  width: 1.5rem;
}
.button.remove {
  background-color: #f04040;
  width: 1.5rem;
}
.button.badge {
  background-color: #f04040;
  border: none;
  color: #ffffff;
  width: 1.5rem;
}

.scroll-500{
  max-height: 500px;
  overflow-y: scroll
}

.max-w-230{
  max-width: 230px;
}
.max-h-150{
  height: 150px;
}
.object-fit-cover{
  object-fit: cover;
}

.font-size10px{
  font-size: 10px
}